div#portfolio {
  overflow: hidden;
}

/* Portfolio background - dots pattern */
.browser-viewport div#portfolio {
  background: linear-gradient(135deg, #ffe6ff 0%, #e6ffe6 100%);
  position: relative;
}

.browser-viewport div#portfolio::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(circle, rgba(150, 100, 200, 0.3) 2px, transparent 2px);
  background-size: 20px 20px;
  pointer-events: none;
}
.portfolio-nav {
  position: absolute;
  font-size: 30px;
  top: 48%;
}
.portfolio-nav.left {
  left: 40px;
}
.portfolio-nav.right {
  right: 36px;
}
.projects {
  margin: 40px 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  min-height: 260px;
}
.project {
  width: 100%;
  min-height: 250px;
  text-align: center;
}
.project-image {
  min-height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.project-image img {
  border-radius: 20%;
  width: 160px;
  height: 160px;
  object-fit: cover;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.project-image img.loading {
  opacity: 0;
}
.project-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.project-info h2 {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
}
.project-info p {
  font-size: 12px;
  height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  margin: 10px 20px;
}
.project-counter {
  font-size: 12px;
  margin-top: 10px;
  color: #666;
  font-style: italic;
}
@media screen and (max-width: 540px) {
  .portfolio-nav {
    font-size: 40px;
    top: 42%;
  }
  .project {
    min-height: 200px;
  }
  .project-image {
    min-height: 140px;
  }
  .project-image img {
    width: 120px;
    height: 120px;
  }
  .project-info h2 {
    font-size: 14px !important;
    height: 25px;
  }
  .project-info p {
    height: 50px;
    -webkit-line-clamp: 2;
    margin: 5px 15px;
  }
  .portfolio-nav i.icon {
    font-size: 0.6em;
  }
}

/* Retro Photo Gallery Style */
.browser-viewport div#portfolio {
  background: repeating-conic-gradient(
    from 0deg at 50% 50%,
    #333333 0deg 20deg,
    #444444 20deg 40deg
  );
  padding: 20px;
  min-height: 100%;
  position: relative;
}

.browser-viewport div#portfolio::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(
    0deg,
    transparent,
    transparent 2px,
    rgba(255, 255, 255, 0.03) 2px,
    rgba(255, 255, 255, 0.03) 4px
  );
  pointer-events: none;
}

.browser-viewport div#portfolio .page-content h1 {
  text-align: center;
  color: #ffffff;
  font-size: 24px;
  text-shadow:
    0 0 10px #00ffff,
    0 0 20px #00ffff;
  background: linear-gradient(to right, #000000, #333333, #000000);
  padding: 10px;
  border: 3px solid #silver;
  border-style: ridge;
  margin-bottom: 20px;
  font-family: 'Impact', 'Arial Black', sans-serif;
  letter-spacing: 2px;
}

/* Photo frame for projects */
.browser-viewport .project {
  background: #ffffff;
  border: 10px solid;
  border-image: linear-gradient(45deg, #8b7355, #d2b48c, #8b7355) 1;
  padding: 10px;
  box-shadow:
    5px 5px 15px rgba(0, 0, 0, 0.5),
    inset 2px 2px 5px rgba(0, 0, 0, 0.2);
  position: relative;
  transform: rotate(-1deg);
  transition: all 0.3s;
}

.browser-viewport .project:hover {
  transform: rotate(0deg) scale(1.05);
  box-shadow:
    10px 10px 25px rgba(0, 0, 0, 0.7),
    inset 2px 2px 5px rgba(0, 0, 0, 0.2);
}

/* Polaroid style */
.browser-viewport .project-image {
  background: #f0f0f0;
  border: 2px solid #e0e0e0;
  padding: 10px;
  margin-bottom: 10px;
}

.browser-viewport .project-image img {
  border-radius: 0;
  border: 1px solid #cccccc;
  filter: sepia(10%);
  transition: all 0.3s;
}

.browser-viewport .project-image img:hover {
  filter: sepia(0%);
  transform: scale(1.1);
}

/* Photo caption */
.browser-viewport .project-info {
  background: #fffef0;
  padding: 10px;
  border-top: 1px dashed #cccccc;
}

.browser-viewport .page-content .project-info h2 {
  color: #333333;
  font-family: 'Marker Felt', 'Comic Sans MS', cursive;
  font-size: 16px;
  margin: 5px 0;
}

.browser-viewport .page-content .project-info p {
  color: #666666;
  font-family: 'Courier New', monospace;
  font-size: 10px;
  line-height: 1.4;
}

/* VCR Controls */
.vcr-controls {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
  padding: 15px;
  background: linear-gradient(to bottom, #666666, #333333);
  border: 3px solid;
  border-color: #999999 #000000 #000000 #999999;
  box-shadow: inset 2px 2px 5px rgba(255, 255, 255, 0.2);
}

.vcr-button {
  background: linear-gradient(to bottom, #cccccc, #999999);
  border: 2px solid;
  border-color: #ffffff #666666 #666666 #ffffff;
  padding: 8px 15px;
  font-family: 'Courier New', monospace;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  color: #000000;
  text-shadow: 1px 1px 0px #ffffff;
  min-width: 40px;
}

.vcr-button:hover {
  background: linear-gradient(to bottom, #dddddd, #aaaaaa);
}

.vcr-button:active {
  border-color: #666666 #ffffff #ffffff #666666;
  background: linear-gradient(to bottom, #999999, #cccccc);
}

.vcr-button.play {
  color: #00ff00;
  text-shadow: 0 0 5px #00ff00;
}

/* Navigation arrows as film reels */
.browser-viewport .portfolio-nav {
  background: radial-gradient(circle, #333333, #000000);
  border: 3px solid #silver;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  cursor: pointer;
  animation: spin 3s linear infinite;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.browser-viewport .portfolio-nav:hover {
  animation-play-state: paused;
  background: radial-gradient(circle, #555555, #222222);
  transform: scale(1.1);
}

.browser-viewport .portfolio-nav i {
  animation: spin-reverse 3s linear infinite;
}

@keyframes spin-reverse {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

/* Loading state */
.browser-viewport .project-info i.spinner {
  color: #00ffff;
  text-shadow: 0 0 10px #00ffff;
}

/* Slideshow indicator */
.slideshow-indicator {
  text-align: center;
  margin: 15px 0;
  padding: 10px;
  background: #000000;
  border: 2px solid #00ff00;
  color: #00ff00;
  font-family: 'Courier New', monospace;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* Film strip decoration */
.film-strip {
  height: 40px;
  background: repeating-linear-gradient(
    90deg,
    #000000 0px,
    #000000 30px,
    #ffffff 30px,
    #ffffff 35px,
    #000000 35px,
    #000000 40px
  );
  border-top: 5px solid #000000;
  border-bottom: 5px solid #000000;
  margin: 20px 0;
  position: relative;
}

.film-strip::before,
.film-strip::after {
  content: '';
  position: absolute;
  top: 10px;
  width: 100%;
  height: 20px;
  background: repeating-linear-gradient(
    90deg,
    transparent 0px,
    transparent 35px,
    #333333 35px,
    #333333 40px
  );
}

/* Gallery view link button */
.gallery-view-btn {
  display: inline-block;
  background: linear-gradient(135deg, #ffd700, #ffed4e);
  color: #000000;
  padding: 5px 15px;
  font-size: 11px;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid #000000;
  cursor: pointer;
  animation: shine 2s linear infinite;
  margin: 10px;
}

@keyframes shine {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 200% 50%;
  }
}

.browser-viewport .project-counter {
  color: #00ff00;
  font-family: 'Courier New', monospace;
  background: #000000;
  padding: 5px 10px;
  display: inline-block;
  border: 1px solid #00ff00;
  margin-top: 15px;
}
